@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,600;1,500&display=swap');

.display {
    width: 99.9%;
    border-collapse: separate;
    border-spacing: 0 7px;
    min-width: 800px;
}    
th {
  
  text-align: left;
  padding: 5px 8px;
}
th {
  background: #42515F 0% 0% no-repeat padding-box;
  border-radius: 0px;
  opacity: 1;
  text-align: left;
  font: normal normal normal 14px/24px Roboto;
  color: #ffffff;
  letter-spacing: 0px;
  text-transform: uppercase;
  border: 0px solid #dddddd;
  padding: 8px;
}
td {
  font: normal normal normal 14px/21px Roboto;
  letter-spacing: 0px;
  color: #3d3d3d;
  opacity: 1;
}
td:first-child {
  display: flex;
  align-items: center;
}
td img:first-child {
  margin-right: 10px;
}
td img {
  cursor: pointer;
}
.profile {
  width: 40px;
  border-radius: 40%;
  margin: 0 60px;
}
.gender {
  text-transform: capitalize;
}
.dept-label {
  background-color: #e9fea5;
  border-radius: 40%;
  width: fit-content;
  padding: 1px 7px;
  text-align: center;
  font-size: 14px;
  display: inline-block;
  font-weight: 300;
  margin-bottom: 8px;
}

tr:not(:first-child) {
background: #ffffff 0% 0% no-repeat padding-box;
border: 2px solid #e3e3e3;
}
